<script>
  import { fly } from "svelte/transition";
  import { createEventDispatcher } from "svelte";
  import answersStore from "./stores/answers-store";
  const dispatch = createEventDispatcher();

  export let delay;
  export let question;
  export let answer;
  export let can_vote;
  export let show_results;
  export let user_code;
  let selected = false;
  let showCantVoteMessage = false;

  function showWarning() {
    showCantVoteMessage = true;
    setTimeout(() => {
      showCantVoteMessage = false;
    }, 1000);
  }
  function voteForAnswer() {
    if (can_vote) {
      if (answer.user.code === user_code) {
        showWarning();
      } else {
        selected = true;
        const game_question = {};
        const vote = {
          user_code: user_code,
          game_id: answer.game_id,
          answer_id: answer.id,
          question_id: question.id
        };
        answersStore.createVote(vote).then(newvote => {
          dispatch("voted", "voted");
          return true;
        });
      }
    }
  }
</script>

<style>
  .answer {
    cursor: pointer;
    display: block;
    background-color: #f1f1f1;
    padding: 20px;
    border-radius: 5px;
    /* 
  
    margin: 0 0 10px;
    font-size: 2em;
    text-decoration: none; */
  }

  .answer:hover {
    background-color: #eee;
  }

  .answer.selected {
    background-color: #23f;
    color: white;
  }

  .answer.correct {
    background-color: #3f2;
    color: black;
  }

  .cant_vote_for_self {
    position: absolute;
    top: 100px;

    background: red;
    color: white;
    border-radius: 5px;
    left: 20%;
    right: 20%;
  }
</style>

<a
  href="#vote"
  in:fly={{ delay: delay, duration: 500, y: 20, opacity: 0 }}
  class="answer"
  class:selected
  class:correct={show_results && answer.correct}
  on:click|preventDefault={voteForAnswer}>
  {answer.text}
</a>

{#if showCantVoteMessage}
  <div class="cant_vote_for_self">
    <p>You cant vote for yours!</p>
  </div>
{/if}
